body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;

}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  display: flex;
  width: 761px;
  height: 582px;
  justify-content: center;
  align-items: center;
  z-index: 1;
  margin-block: 4rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}

h2 {
  text-align: center;
}

.potrait img {
  width: 361px;
  height: 582px;
  background-size: cover;
  overflow: hidden;
}

.potrait {

  width: 361px;
  height: 582px;
  justify-content: center;
  align-items: center;
  position: relative;

}

.potrait {
  height: 582px;
  position: relative;

}

.potrait:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  border-bottom: 80px solid white;
  border-right: 80px solid transparent;
  width: 0;

}

.potrait_content {
  position: absolute;
  top: 0px;
  color: #fff;
  text-align: start;
  margin-block: 5rem;
}

.potrait_content>h1 {
  text-align: center;

}

.potrait_content>p {
  padding-inline: 3rem;
}

.inputfield {
  width: 290px;
  height: 42px;
  border: 1px solid black;
}

.content {

  width: 400px;
  height: 582px;
  padding: 0 18px 0 18px;
  background-color: #fff;


}

.content {
  height: 582px;
  position: relative;

}

.content:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  border-top: 80px solid #C7ACDA;
  border-left: 80px solid transparent;
  width: 0;

}



.content>form {
  justify-content: space-around;
  
}

.btn {
  width: 188px;
  height: 44px;
  border-radius: 4px;
  background-color: #2D9CDB;
}


.flex {
  display: flex;
  flex-direction: column;
}

.custom-shape-divider-top-1681672401 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

.custom-shape-divider-top-1681672401 svg {
  position: relative;
  display: block;
  width: calc(290% + 1.3px);
  height: 500px;
}

.custom-shape-divider-top-1681672401 .shape-fill {
  fill: #C7ACDA;
}

@media  screen  and (max-width: 600px) {
  
  .App {
    width: 100%;
    /* height: 750px; */
  
 }

  .main {
    display: flex;
    flex-direction: column;

    width: 100%;
    box-shadow: none;
    /* margin-bottom: 50px; */
    
  }
  

  .potrait {
    width: 100%;
  
  }

  .potrait>img {
    height: 270px;
    width: 100%;
    background-size: cover;
    margin: 0;
  }

  .potrait_content {

    margin-block: 2rem;

  }

  .custom-shape-divider-top-1681672401 svg {
    display: none;
  }

  .content {
    width: 99%;
    
    
  }

  .content > form {
    align-items: center;
    height: 450px;
    gap: 20;
    
  
  }
  .inputfield {
    width: 290px;
    height: 35px;
   
  }

  .content:before {
    border: none;
  }

  .potrait:before {
    border: none;
  }

.svgg {
  position: absolute;
  /* top: 500; */
  margin-top: 800px;
  height: 100px;
}

.custom-shape-divider-bottom-1681827943 {
  z-index: 2222222222;
  position: relative;
  top: 5000;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);


}

.custom-shape-divider-bottom-1681827943 svg {
    position: relative;
    display: block;
    width: calc(287% + 1.3px);
    height: 238px;
}

.custom-shape-divider-bottom-1681827943 .shape-fill {
    fill: #C7ACDA;
}
